window.App = window.App || {};

window.App.Sessions = {
  form: null,

  init() {
    this.form = document.getElementById('new-session-form');

    this.form.addEventListener('ajax-error', (event) => {
      console.log('got error');
    });

    Validation.init(this.form, true);
  }
};

document.addEventListener('turbolinks:load', () => window.App.Sessions.init());


